:root {
  --font-body: Arial, sans-serif;
  --font-bai-jamjuree: "Bai Jamjuree", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --strongcyan: hsl(171, 66%, 44%);
  --lightblue: hsl(233, 100%, 69%);
  --darkgrayishblue: hsl(210, 10%, 33%);
  --grayishblue: hsl(201, 11%, 66%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-bai-jamjuree);
  font-size: 1.125rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header {
  background-image: url("../images/bg-header-mobile.png");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: 100%;
  overflow: auto;
}
@media (min-width: 40em) {
  .header {
    background-image: url("../images/bg-header-desktop.png");
  }
}
.header__logo {
  display: block;
  margin: 0 auto;
  margin-top: 7.6875rem;
}
@media (min-width: 40em) {
  .header__logo {
    margin-top: 8.1875rem;
  }
}
.header__title {
  font-weight: 600;
  font-size: 1.9375rem;
  text-align: center;
  color: var(--darkgrayishblue);
  max-width: 21.875rem;
  margin: 0 auto;
  margin-top: 4.125rem;
  padding: 0 0.625rem;
  line-height: 2.4375rem;
}
@media (min-width: 40em) {
  .header__title {
    font-size: 2.8125rem;
    max-width: unset;
    padding: 0 6.5625rem;
  }
}
.header__description {
  font-size: 1rem;
  text-align: center;
  color: var(--grayishblue);
  line-height: 1.625rem;
  margin: 0 auto;
  margin-top: 1rem;
  padding: 0 2.0625rem;
}
@media (min-width: 40em) {
  .header__description {
    font-size: 1.25rem;
    max-width: 48.125rem;
    margin-top: 1.625rem;
    line-height: 1.8125rem;
  }
}